import React, { useEffect, useState } from 'react';
import {
  Button,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  UncontrolledDropdown,
} from 'reactstrap';
import { Colxx } from '../../../components/common/CustomBootstrap';
import backendUrl from '../../../utils/urls';

const NewDestination = () => {
  const [dropdownSplitOpen, setDropdownSplitOpen] = useState(false);
  const [destinations, setDestinations] = useState([]);
  const [select, setSelect] = useState('');
  useEffect(() => {
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
    };
    fetch(`${backendUrl}/api/v1/destination_definitions/list`, requestOptions)
      .then((res) => res.json())
      .then((res) => setDestinations(res.destinationDefinitions));
  }, []);
  const toggle = () => setDropdownSplitOpen((prevState) => !prevState);
  return (
    <Form>
      <Row>
        <Colxx xxs="12">
          <h1>Create a Destination</h1>
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12">
          <FormGroup>
            <Label for="destinationName">Name of Destination</Label>
            <Input
              type="text"
              name="destination"
              id="destinationName"
              placeholder="Enter name of source"
            />
          </FormGroup>
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12">
          <UncontrolledDropdown isOpen={dropdownSplitOpen} toggle={toggle}>
            <DropdownToggle caret>{select}</DropdownToggle>
            <DropdownMenu>
              {destinations.map((element) => (
                <DropdownItem
                  onClick={() => setSelect(element.name)}
                  key={destinations.indexOf(element)}
                >
                  {element.name}
                </DropdownItem>
              ))}
            </DropdownMenu>
          </UncontrolledDropdown>
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12">
          <Button className="my-4" color="primary">
            Create a destination
          </Button>{' '}
        </Colxx>
      </Row>
    </Form>
  );
};

export default NewDestination;
